.container {
    display: flex;
    width: 100%;
    flex: 1 0 auto;
    height: 0;
}

.video_container {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
}

video {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.content {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    gap: 30rem;
    pointer-events: none;
    z-index: 2;
}

.logo {
    display: flex;
    height: 100%;
    width: fit-content;
    padding: 50rem;
}

.logo img {
    object-fit: scale-down;
    height: 100%;
}

.text {
    display: flex;
    font-size: 75rem;
    color: rgb(22, 22, 22);
    justify-content: center;
    align-items: center;
    text-wrap: pretty;
    text-align: center;
    width: 30%;
}