.container {
    display: flex;
    width: 100%;
    flex: 1 0 auto;
    height: 0;
    flex-flow: column;
}

.header {
    font-size: 2em;
    font-weight: bold;
    width: 100%;
    padding: 20rem;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.content {
    display: flex;
    flex-flow: row;
    width: 100%;
    height: 0;
    flex: 1 0 auto;
}

.image {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 100%;
    flex: 0 0 auto; 
    padding: 50rem;
    overflow: hidden;
}

.image img {
    border-radius: 50rem;
    object-fit:cover;
    width: 100%;
    height: 100%;
}

.text {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 50rem;
    width: 100%;
    height: 100%;
    text-wrap: pretty;
}