.container {
    display: flex;
    /* position: absolute; */
    justify-content: center;
    width: 100%;
    height: 80rem;
    /* top: 0; */
    /* left: 0; */
    padding: 0 10rem 10rem 10rem;
    z-index: 3;
}

.container ul {
    display: flex;
    width: fit-content;
    height: 100%;
    padding: 0 20rem;
    justify-content: center;
    align-items: center;
    background-color: rgba(0,0,0, 0.8);
    border-radius: 0 0 20rem 20rem;
    gap: 20rem;
}

.container ul li {
    all: unset;
    color: rgb(255, 255, 255, 0.8);
    padding: 8rem;
}

.container ul li:hover {
    color:white;
    cursor: pointer;
    background-color: rgb(255, 255, 255, 0.08);
    border-radius: 10rem;
}

.container ul li.active {
    color: white;
    text-decoration: underline;
}